import React,{useState,useContext} from "react";
import {Dropdown,DropdownToggle,DropdownMenu,DropdownItem} from "reactstrap";
import user4 from "../../../assets/images/users/avatar-6.jpg"
import {Link } from "react-router-dom"
import { useNavigate } from "react-router-dom";
import {AuthDispatchContext,logoutSuccess} from "../../../context/AuthContext"
const ProfileMenu = ()=>{
  const navigate = useNavigate();
  const dispatch = useContext(AuthDispatchContext);
  const signOut =(e)=>{
    e.preventDefault()
    logoutSuccess(dispatch)
    navigate("/")

  }

  const [menu,setMenu] = useState(false);
  return(<React.Fragment>
          <Dropdown
            isOpen={menu}
            toggle={()=>setMenu(!menu)}
            className="d-inline-block"
          >
            <DropdownToggle
              className="btn header-item waves-effect"
              id="page-header-user-dropdown"
              tag="button"
              >
              <img className="rounded-circle header-profile-user" src={user4} alt="Header Avatar" />{" "}
              <span className="d-none d-xl-inline-block ms-1 text-white">Harish</span>{" "}
              <i className="mdi mdi-chevron-down d-none d-xl-inline-block text-white"></i>{" "}
            </DropdownToggle>
            <DropdownMenu className="dropdown-menu-end">
              <DropdownItem tag="a" href="/profile">
                {" "}
                <i className="bx bx-user font-size-16 align-middle me-1"></i>{" "}View Profile
              </DropdownItem>
              <DropdownItem tag="a" href="auth-lock-screen">
                  <i className="bx bx-lock-open font-size-16 align-middle me-1"></i>{" "}Lock screen
              </DropdownItem>
              <div className="dropdown-divider" />
              <Link to="/#" onClick={signOut} className="dropdown-item text-danger">
                <i className="bx bx-power-off font-size-16 align-middle me-1 text-danger"></i>{" "}
                <span>Logout</span>
              </Link>
            </DropdownMenu>
          </Dropdown>
        </React.Fragment>)

}

export default ProfileMenu;
