import React,{useContext,useState,useEffect} from "react";
import {Collapse} from "reactstrap";
import {Link} from "react-router-dom"
import {LayoutStateContext} from "../../context/LayoutContext";

const Navbar = ()=>{
  const state = useContext(LayoutStateContext);
  const [email,setEmail] = useState(false);

  useEffect(() => {
        var matchingMenuItem = null
        var ul = document.getElementById("navigation")
        var items = ul.getElementsByTagName("a")
        for (var i = 0; i < items.length; ++i) {
            matchingMenuItem = items[i]
        }
        if (matchingMenuItem) {
          activateParentDropdown(matchingMenuItem)
        }
      })
  function activateParentDropdown(item){
    item.classList.add("active")
    const parent = item.parentElement
    if (parent) {
      parent.classList.add("active") // li
      const parent2 = parent.parentElement
      parent2.classList.add("active") // li
      const parent3 = parent2.parentElement
      if (parent3) {
        parent3.classList.add("active") // li
        const parent4 = parent3.parentElement
        if (parent4) {
          parent4.classList.add("active") // li
          const parent5 = parent4.parentElement
          if (parent5) {
            parent5.classList.add("active") // li
            const parent6 = parent5.parentElement
            if (parent6) {
              parent6.classList.add("active") // li
            }
          }
        }
      }
    }
    return false

  }


  return(<React.Fragment>
          <div className="topnav">
            <nav className="navbar navbar-light navbar-expand-lg topnav-menu" id="navigation">
            <Collapse
              isOpen={state.leftMenu}
              className="navbar-collapse"
              id="topnav-menu-content"
              >
              <ul className="navbar-nav">
                <li className="nav-item dropdown">
                  <Link className="nav-link dropdown-toggle arrow-none" to="/dashboard">
                    Dashboard
                  </Link>
                </li>
                <li className="nav-item dropdown">
                  <Link className="nav-link dropdown-toggle arrow-none" to="calendar">
                    Calendar
                  </Link>
                </li>
                <li className="nav-item dropdown">
                    <Link
                      className="nav-link dropdown-toggle arrow-none"
                      to="/#"
                      onClick={e => {e.preventDefault()
                        setEmail(!email)
                      }}
                       >
                      Email<div className="arrow-down"></div>
                    </Link>
                    <div className={email?`dropdown-menu show`:`dropdown-menu`}>
                      <Link to="/dashboard" className="dropdown-item">
                          Inbox
                      </Link>
                      <Link to="/dashboard2" className="dropdown-item">
                          Read Email
                      </Link>
                    </div>
                </li>
              </ul>
            </Collapse>
            </nav>
          </div>
        </React.Fragment>)

}

export default Navbar;
