import React,{useState} from "react";
import {Link} from "react-router-dom";
import {Container,Row,Col,Card,Form,FormGroup,Label,Input} from "reactstrap";
import logo from "../../assets/images/logo-sm-dark.png"
import {post} from "../../helpers/api_helpers";
import {validateEmail} from "../../helpers/validator"
const Register =()=>{
  const [registerUser,setRegisterUser] = useState({name:"",email:"",password:"",confirmPassword:""});
  const [response, setResposne] = useState(false);
  const [serverResponse,setServerResponse] = useState({});
  const [error,setError] = useState({name:false,email:false,password:false});
  const backendUrl = process.env.NODE_ENV === "development"?(process.env.REACT_APP_BACKEND_URL):(process.env.REACT_APP_BACKEND_URL_PRODUCTION)
  const handleSumbit =(e)=>{
      const name = e.target.name;
      const value = e.target.value;
      setRegisterUser({...registerUser,[name]:value})
  }
  // validating the field data
  const InputBlurHandler =(e)=>{
    if(e.target.name=== "email"){
         if(e.target.value.trim()==='' || !validateEmail(e.target.value)){
           return setError({...error,[e.target.name]:true})
         }
         return setError({...error,[e.target.name]:false})
    }
    else if(e.target.name === "name"){
      if(e.target.value.trim()===''){
        return setError({...error,[e.target.name]:true})
      }
       return setError({...error,[e.target.name]:false})
    }
    else if(e.target.name === "confirmPassword"){
        if(registerUser.password !== registerUser.confirmPassword ){
          return setError({...error,password:true})
        }
        return setError({...error,password:false})
    }
  }

// form validation ends here

// subbmitting the form data

  const submitData = (e)=>{
    e.preventDefault();
    if(registerUser.name.trim() === "" || registerUser.email.trim() === "" || registerUser.password.trim()=== "" ){
        let errorObj = error;
        if(registerUser.name.trim() === ""){
          errorObj = {...errorObj,name:true}
        }
        if(registerUser.email.trim() === ""){
          errorObj = {...errorObj,email:true}
        }
        if(registerUser.password.trim()=== ""){
          errorObj = {...errorObj,password:true}
        }
        return setError(errorObj)
    }
    else{
      post(backendUrl+'/api/auth/signup',registerUser)
      .then((res)=>{
        setServerResponse(res)
        setResposne(true)
        setRegisterUser({name:"",email:"",password:"",confirmPassword:""})
        setTimeout(()=>{
          setResposne(false)
        },3500)

      })
      .catch((err)=>console.log(err.response))

    }
  }

//form subbmisson ends herecd

  return(<React.Fragment>
          <div className="account-pages pt-sm-5">
            <Container>
              <Row className="justify-content-center">
                <Col md={8} lg={6} xl={5}>
                  <Card className="overflow-hidden">
                    <div className="bg-login text-center">
                      <div className="bg-login-overlay"></div>
                        <div className="position-relative">
                          <h5 className="text-white font-size-20">Free Register</h5>
                          <p className="text-white-50 mb-0">Open Instant account now</p>
                          <Link to="/" className="logo logo-admin ">
                            <img src={logo} alt="" height="30" />
                          </Link>
                        </div>
                      </div>
                      <div className="card-body pt-2">
                        <div className="p-2">
                          <Form onSubmit={submitData} >
                            <FormGroup className="mb-3">
                              <Label for="exampleEmail">Email</Label>
                              <Input
                                  type="email"
                                  name="email"
                                  placeholder="Enter Email"
                                  value={registerUser.email}
                                  onChange={handleSumbit}
                                  onBlur={InputBlurHandler}
                              />
                            {error.email?
                              (<div className="alert alert-danger" role="alert">
                                  Please Enter the Valid Email
                              </div>):(<div></div>) }
                            </FormGroup>
                            <FormGroup className="mb-3">
                              <Label for="username">Username</Label>
                              <Input
                                type="text"
                                name="name"
                                value={registerUser.name}
                                onChange={handleSumbit}
                                onBlur={InputBlurHandler}
                                placeholder="Enter Username"
                              />
                            {error.name?
                                (<div className="alert alert-danger" role="alert">
                                    Please Enter Username
                                </div>):(<div></div>) }
                            </FormGroup>
                            <FormGroup className="mb-3">
                              <Label for="username">Password</Label>
                              <Input
                                type="password"
                                name="password"
                                value={registerUser.password}
                                placeholder="Enter Password"
                                onChange={handleSumbit}
                                autoComplete="true"
                                />
                            </FormGroup>
                            <FormGroup className="mb-3">
                              <Label for="username">Confirm Password</Label>
                              <Input
                                  type="password"
                                  name="confirmPassword"
                                  value={registerUser.confirmPassword}
                                  onChange={handleSumbit}
                                  onBlur={InputBlurHandler}
                                  placeholder="Renter Password"
                                  autoComplete="true"
                              />
                            {error.password?
                                  (<div className="alert alert-danger" role="alert">
                                      Password and confirm password does not match
                                  </div>):(<div></div>) }
                            </FormGroup>
                            <div className="mt-4">
                              <button className="btn btn-primary w-100 waves-effect waves-light" type="submit">
                                Register
                              </button>
                            </div>
                            {response &&
                              Object.values(serverResponse).map((message,idx)=>(
                                <div className="alert alert-warning mt-2 text-center fs-5" role="alert" key={idx}>
                                    {message}
                                </div>
                              ))}


                          </Form>
                        </div>
                      </div>
                  </Card>
                  <div className="mt-5 text-center">
                      <p>Already have an account?
                        <a href="/" className="fw-medium text-primary"> Login</a>
                      </p>
                  </div>
                </Col>
              </Row>
            </Container>
          </div>
        </React.Fragment>)
}
export default Register;
