import {useEffect,useRef,useState,useContext} from "react";
import {AuthStateContext} from "../../../context/AuthContext";
import {useLocation} from 'react-router-dom';
const Header = ()=>{
  const headerTop =  useRef();
  const state = useContext(AuthStateContext);
  const location = useLocation();

  const [mobileNav,setMobileNav] = useState(false);
  const scrollFunction = ()=>{
        if(location.pathname === "/"){
          window.addEventListener('scroll', handleScroll)
        }
        else{
          headerTop.current.classList.add("sticky")
        }
  }



const handleScroll = ()=>{
  const scrollY = window.scrollY
  if(scrollY > 100){
    headerTop.current.classList.add("sticky")
  }
  else{
    headerTop.current.classList.remove("sticky")
  }

}

const scrollEvent = (e)=>{
  e.preventDefault()
  setMobileNav(false)
  let targetElement= document.getElementById(e.target.href.split('#')[1])
  targetElement.scrollIntoView();
  // e.target.scrollIntoView()

}


  useEffect(scrollFunction,[location.pathname])
  return(<>
          <section className="header_area">
            <div className="header_navbar" ref={headerTop} >
              <div className="container">
                <div className="row">
                  <div className="col-lg-12">
                    <nav className="navbar navbar-expand-lg">
                      <a className="navbar-brand" href="index.html">
                        <img src="./developer.png" alt="Logo"  />
                      </a>
                      <button className={mobileNav?(`navbar-toggler active`):(`navbar-toggler`)} type="button"  onClick={()=>setMobileNav(!mobileNav)} >
                        <span className="toggler-icon"></span>
                        <span className="toggler-icon"></span>
                        <span className="toggler-icon"></span>
                      </button>
                      <div className={mobileNav?(`collapse navbar-collapse sub-menu-bar show`):(`collapse navbar-collapse sub-menu-bar`)}>
                          <ul id="nav" className="navbar-nav ms-auto">
                            <li className="nav-item active">
                              <a className="page-scroll" href="#home" onClick={scrollEvent}>Home <span></span></a>
                            </li>
                            {/*
                            <li className="nav-item">
                              <a className="page-scroll" href="#about" onClick={scrollEvent}>About<span></span></a>
                            </li>
                            <li className="nav-item">
                              <a className="page-scroll" href="#blogs" onClick={scrollEvent} >Blogs<span></span></a>
                            </li>
                            <li className="nav-item">
                              <a className="page-scroll" href="#work" onClick={scrollEvent}>Experience<span></span></a>
                            </li>*/}
                            {state.isAuthenticated?(<>
                              <li className="nav-item">
                                <a className="page-scroll" href="#/dashboard">Dashboard<span></span></a>
                              </li>
                              </>):(
                              <>
                              <li className="nav-item">
                                <a className="page-scroll" href="#/login">Login<span></span></a>
                              </li>
                  
                              <li className="nav-item">
                                <a className="page-scroll" href="#/register">Register<span></span></a>
                              </li>
                              </>
                            )}
                          </ul>
                      </div>
                    </nav>
                  </div>
                </div>
              </div>
            </div>
          </section>
         </>)
}
export default Header;
