import React,{useContext,useEffect,useState} from "react";
import Header from "./Header";
import Sidebar from "./Sidebar";
import Footer from "./Footer";
import Rightbar from "../CommonForBoth/Rightbar"
import {LayoutStateContext,LayoutDispatchContext,changeSidebarType,changeSidebarTheme,changeLayout,changeLayoutWidth,changeTopbarTheme} from "../../context/LayoutContext";

const Layout =({children})=>{
  const state = useContext(LayoutStateContext);
  const dispatch = useContext(LayoutDispatchContext);
  const [isMobile,setIsMobile] = useState(false);
  useEffect(()=>{
    let device = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent)
    setIsMobile(device)
    toggleMenuCallback()
  },[])// eslint-disable-line react-hooks/exhaustive-deps

  const toggleMenuCallback =()=>{
      if(state.leftSideBarType === "default"){
        changeSidebarType(dispatch,"condensed",isMobile)
      }
      else if(state.leftSideBarType === "condensed"){
        changeSidebarType(dispatch,"default",isMobile)
      }
  }

  useEffect(()=>{
      if (state.isPreloader === true) {
        document.getElementById("preloader").style.display = "block"
        document.getElementById("status").style.display = "block"
        setTimeout(function () {
          document.getElementById("preloader").style.display = "none"
          document.getElementById("status").style.display = "none"
          }, 2500)
      }
      else{
        document.getElementById("preloader").style.display = "none"
        document.getElementById("status").style.display = "none"
      }
      window.scrollTo(0, 0)
      if(state.leftSideBarType){
        changeSidebarTheme(dispatch,state.leftSideBarType)
      }
      changeLayout(dispatch,"detached")
      if(state.layoutWidth){
        changeLayoutWidth(dispatch,state.layoutWidth)
      }
      if(state.leftSideBarType){
        changeSidebarType(dispatch,state.leftSideBarType)
      }
      if(state.topbarTheme){
        changeTopbarTheme(dispatch,state.topbarTheme)
      }
  },[]) // eslint-disable-line react-hooks/exhaustive-deps


  return(<React.Fragment>
          <div id="preloader">
            <div id="status">
              <div className="spinner-chase">
                <div className="chase-dot"></div>
                <div className="chase-dot"></div>
                <div className="chase-dot"></div>
                <div className="chase-dot"></div>
                <div className="chase-dot"></div>
                <div className="chase-dot"></div>
              </div>
            </div>
          </div>
          <div className="container-fluid">
            <div id="layout-wrapper">
              <Header toggleMenuCallback={toggleMenuCallback} />
              <Sidebar />
              <div className="main-content">
                  {children}
                <Footer />
              </div>
            </div>
          </div>
          {state.showRightSidebar?(<Rightbar />):(null)}
        </React.Fragment>
      )


}

export default Layout;
