import React,{useState} from "react"
import {Link,useParams} from "react-router-dom";
import logo from "../../assets/images/logo-sm-dark.png"
import {Container,Row,Col,Card,Form,FormGroup,Label,Input,Alert} from "reactstrap";
import {parseJwt} from "../../context/AuthContext";
import {post} from "../../helpers/api_helpers";

const PasswordReset =()=>{
  const {token} = useParams();
  let resetToken = token.replace(":","");
  let resetData =  parseJwt(resetToken);
  const backendUrl = process.env.NODE_ENV === "development"?(process.env.REACT_APP_BACKEND_URL):(process.env.REACT_APP_BACKEND_URL_PRODUCTION)
  const [message,setMessage] = useState(null);
  const [userData,setUserData] = useState({email:resetData.email,password:"",confirmPassword:"",token:resetToken})
  const [passwordError,setPasswordError] = useState(null);

  const handleChange = (e)=>{
    const name = e.target.name;
    const value = e.target.value;
    setUserData({...userData,[name]:value})
  }

  // checking It Password Match
  const InputBlurHandler =(e)=>{
    if(e.target.name === "confirmPassword"){
        if(userData.password !== userData.confirmPassword ){
          setPasswordError("Password and confirm password does not match")
      }
      else{
        setPasswordError(null)
      }
    }
  }

  // submitting the reset Password
  const submitData = (e)=>{
    e.preventDefault()
    if(userData.password.trim() === "" || userData.password !== userData.confirmPassword ){
      setPasswordError("Password Field Cannot be Empty ")
    }
    else{
      post(backendUrl+'/api/auth/passwordUpdate',userData)
      .then((res)=>{
        setMessage(res.message)
      })
    }

  }
  return(<React.Fragment>
            <div className="account-pages pt-sm-5">
              <Container>
                {message? (
                    <Alert color="success" className="text-center mb-4" style={{ marginTop: "13px" }}>
                        {message}
                        <div className="mt-5 text-center">
                            <p>Click On Link To Login
                              <a href="/" className="fw-medium text-primary"> Login</a>
                            </p>
                        </div>
                      </Alert>
                    ) :
                    (
                      <Row className="justify-content-center">
                        <Col md={8} lg={6} xl={5}>
                          <Card className="overflow-hidden">
                            <div className="bg-login text-center">
                              <div className="bg-login-overlay"></div>
                              <div className="position-relative">
                                <h5 className="text-white font-size-20">Enter New Password</h5>
                                <Link to="/" className="logo logo-admin ">
                                    <img src={logo} alt="" height="30" />
                                </Link>
                              </div>
                            </div>
                            <div className="card-body pt-2">
                              <div className="p-2">
                                <Form onSubmit={submitData} >
                                  <FormGroup className="mb-3">
                                    <Label for="exampleEmail">Email</Label>
                                      <Input
                                          type="email"
                                          name="email"
                                          defaultValue={userData.email}
                                          disabled
                                      />
                                  </FormGroup>
                                  <FormGroup className="mb-3">
                                    <Label for="password">Password</Label>
                                      <Input
                                        type="password"
                                        name="password"
                                        onChange={handleChange}
                                        placeholder="Enter Password"
                                        />
                                  </FormGroup>
                                  <FormGroup className="mb-3">
                                    <Label for="password">Confirm Password</Label>
                                      <Input
                                        type="password"
                                        name="confirmPassword"
                                        placeholder="Renter Password"
                                        onChange={handleChange}
                                        onBlur={InputBlurHandler}
                                        />
                                        {passwordError?
                                            (<div className="alert alert-danger" role="alert">
                                                {passwordError}
                                            </div>):(null) }
                                  </FormGroup>
                                  <div className="mt-4">
                                    <button className="btn btn-primary w-100 waves-effect waves-light" type="submit">
                                      Submit
                                    </button>
                                  </div>
                                </Form>
                              </div>
                            </div>
                          </Card>
                        </Col>
                      </Row>
                    )
                  }
              </Container>
            </div>
        </React.Fragment>)

}

export default PasswordReset;
