import React,{useState,useContext} from "react";
import { Link } from "react-router-dom";
import {Container,Row,Col,Form,FormGroup,Label,Input} from "reactstrap";
import {post} from "../../helpers/api_helpers";
import logo from "../../assets/images/logo-sm-dark.png";
import {loginSuccess,loginError,AuthDispatchContext,AuthStateContext} from "../../context/AuthContext";
import { useNavigate } from "react-router-dom";
const Login = (props)=>{
    const navigate = useNavigate();
    const [user,setUser] = useState({email:"",password:""});
    const [error,setError] = useState({email:"",password:""});
    const backendUrl = process.env.NODE_ENV === "development"?(process.env.REACT_APP_BACKEND_URL):(process.env.REACT_APP_BACKEND_URL_PRODUCTION)
    //handling the Form Change
    const dispatch = useContext(AuthDispatchContext);
    const state = useContext(AuthStateContext);


    const handleChange = (e)=>{
      const name = e.target.name;
      const value = e.target.value;
      setUser({...user,[name]:value})
      setError({...error,[name]:""})
    }

    // submitting the Form
    const submitForm = (e)=>{
      e.preventDefault();
      if(user.email.trim() === "" || user.password.trim() === ""){
         let errorObj = error;
         if(user.email.trim() === ""){
          errorObj = {...errorObj,email:"Email Field Cannot Be Empty"}
         }
         if(user.password.trim()===""){
           errorObj = {...errorObj,password:"Enter A Valid Password"}
         }
         setError(errorObj)
      }
      else{
        post(backendUrl+'/api/auth/signIn',user)
        .then((res)=>{
          loginSuccess(dispatch,res.access,res.refresh)
          navigate("/dashboard");
        })
        .catch((err)=>
          loginError(dispatch,err.response.data.error))
      }

    }




    return(
      <React.Fragment>
        <div className="account-pages my-2 pt-sm-5">
          <Container>
            <Row className="justify-content-center">
              <Col md={8} lg={6} xl={5}>
                <div className="card overflow-hidden">
                   {/*Card Header */}
                  <div className="bg-login text-center">
                    <div className="bg-login-overlay"></div>
                      <div className="position-relative">
                        <h5 className="text-white font-size-20">Welcome Back !</h5>
                        <p className="text-white-50 mb-0">Sign in to continue </p>
                        <Link to="/" className="logo logo-admin ">
                          <img src={logo} alt="" height="30" />
                        </Link>
                      </div>
                   </div>
                   {/*Card Header Ends */}
                   <div className="card-body pt-5">
                   {/*Card Body Starts */}
                    <div className="p-2">
                      <Form onSubmit={submitForm}>
                         <FormGroup className="mb-3">
                           <Label for="exampleEmail">Email</Label>
                           <Input type="email" name="email"  placeholder="Enter Email" onChange={handleChange} />
                           {error.email.trim() !== ""?
                             (<div className="alert alert-danger" role="alert">
                               {error.email}
                           </div>):(<div></div>)}

                         </FormGroup>
                         <FormGroup className="mb-3">
                            <Label for="examplePassword">Password</Label>
                            <Input type="password" name="password"  placeholder="Enter Password" autoComplete="true" onChange={handleChange}/>
                              {error.password.trim() !== ""?
                                (<div className="alert alert-danger" role="alert">
                                  {error.password}
                              </div>):(<div></div>)}

                         </FormGroup>
                         <div className="form-check">
                          <Input type="checkbox"  className="form-check-input"  id="customControlInline" />
                          <Label className="form-check-label" for="customControlInline">Remember me</Label>
                         </div>
                         <div className="mt-3">
                           <button className="btn btn-primary w-100 waves-effect waves-light" type="submit">
                              Log In
                            </button>
                         </div>
                        {state.errorMessage?(
                          <div className="alert alert-danger mt-2 text-center fs-5" role="alert" >
                              {state.errorMessage}
                          </div>
                        ):(<span></span>)}



                         <div className="mt-4 text-center">
                            <Link to="/forgot-password" className="text-muted">
                            <i className="mdi mdi-lock me-1"></i> Forgot your password?
                            </Link>
                         </div>
                      </Form>
                    </div>
                   {/*Card Body Ends */}
                   </div>
                </div>
                <div className="mt-2 text-center">
                  <p>Don't have an account?
                    <Link to="/register" className="fw-medium text-primary ms-2">
                      Signup now
                    </Link>
                  </p>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>)

}

export default Login;
