import React,{useEffect,useContext,useState} from "react";
import Header from "./Header";
import Rightbar from "../CommonForBoth/Rightbar"
import {LayoutStateContext,LayoutDispatchContext,changeLayout} from "../../context/LayoutContext";
const HorizontalLayout =({children})=>{
  const dispatch = useContext(LayoutDispatchContext);
  const state = useContext(LayoutStateContext);
  const [isMenuOpened,openMenu] = useState(false);
  useEffect(()=>{
    changeLayout(dispatch,"horizontal")
  },[dispatch])
  return(<React.Fragment>
            <div className="container-fluid">
              <div id="layout-wrapper">
                <header id="page-topbar">
                  <Header
                      theme={state.topbarTheme}
                      isMenuOpened={isMenuOpened}
                      openLeftMenuCallBack={openMenu}
                     />
                </header>
                <div className="main-content">
                    {children}
                </div>
              </div>
            </div>
            {state.showRightSidebar?(<Rightbar />):(null)}
        </React.Fragment>)

}
export default HorizontalLayout;
