import React,{useContext,useEffect} from "react";
import {Routes, HashRouter, Route} from "react-router-dom";
import {authRoutes,dashboardRoutes,commonRoutes} from "./routes/allRoutes";
import {AuthStateContext,parseJwt,AuthDispatchContext,logoutSuccess,setUserLogin,loginError,setAccessToken} from "./context/AuthContext"
import {LayoutStateContext} from "./context/LayoutContext"
import  Authmiddleware from "./routes/middleware/Authmiddleware";
import axios from "axios";
import VerticalLayout from "./components/VerticalLayout/";
import HorizontalLayout from "./components/HorizontalLayout";
import FrontLayout from "./components/FrontLayout";
import "./assets/scss/theme.scss"
import "./assets/scss/bootstyle.scss"

function App(props){
  const layout = useContext(LayoutStateContext);
  const state = useContext(AuthStateContext);
  const dispatch = useContext(AuthDispatchContext);
  const backendUrl = process.env.NODE_ENV === "development"?(process.env.REACT_APP_BACKEND_URL):(process.env.REACT_APP_BACKEND_URL_PRODUCTION)



  //checking Up Login In Refresh
  const checkingUpLogin =()=>{
    var access = localStorage.getItem("access");
    var refresh = localStorage.getItem("refresh");
    var curTime = new Date();
    curTime = Math.floor(curTime.getTime()/1000.0);
    var refreshExp = false;
    // checking If Refresh Token Is Present
    if(refresh !== null){
        // checking refresh Token Has Expired
        if(curTime > parseJwt(refresh).exp){
            refreshExp = true;
        }
    }
    if(access !== null && refresh !== null && !refreshExp){
      let accessExp = parseJwt(access).exp;
      let hasError = false;
      if(curTime > accessExp){
           axios.post(backendUrl+"/api/auth/refresh",{refresh})
          .then((res)=>{
            setAccessToken(dispatch, res.data.access)
            access = res.data.access
          })
          .catch((err)=>
          loginError(dispatch,err.response.data))
      }
      if(!hasError){
        // setting Up Login when  Refreshing The Page
        setUserLogin(dispatch,access,refresh)
      }
      else{
        // Token Time OF access Token And Refresh Token Has Expired
        logoutSuccess(dispatch)
      }

    }
    else{
      // logout When No Token Is Available
      logoutSuccess(dispatch)
    }


  }
  // Starting The Layout System
  function getLayout() {
    let layoutCls;
    switch (layout.layoutType) {
      case "horizontal":
        layoutCls = HorizontalLayout
        break
      default:
        layoutCls = VerticalLayout
        break
    }
    return layoutCls
  }



  const Layout = getLayout();
  useEffect(checkingUpLogin,[backendUrl,dispatch])

  return (
    <React.Fragment>
      <HashRouter>
            <Routes>
              {!state.isAuthenticated?
                (authRoutes.map((item, idx) => (
                    <Route path={item.path} element={<Authmiddleware component={item.component} layout={FrontLayout}   />} key={idx} />
                ))):
                (
                  dashboardRoutes.map((item, idx) =>(
                    <Route path={item.path} element={<Authmiddleware component={item.component} layout={Layout}  />} key={idx} />
                  )))}
                <Route path={commonRoutes[0].path} element={<Authmiddleware component={commonRoutes[0].component} layout={FrontLayout}  />} />
            </Routes>
      </HashRouter>
    </React.Fragment>
  );
}

export default App;
