import './header.css'
import TypeWriter from './Typewriter';

const HeaderHero = ()=>{

  function calculateExperience(startYear) {
    const startDate = new Date(`${startYear}-01-01`);
    const currentDate = new Date();

    const diffTime = Math.abs(currentDate - startDate);
    const diffYears = currentDate.getFullYear() - startDate.getFullYear();
    const diffMonths = currentDate.getMonth() - startDate.getMonth();
    const diffDays = currentDate.getDate() - startDate.getDate();

    let years = diffYears;
    let months = diffMonths;
    let days = diffDays;

    if (days < 0) {
        months -= 1;
        const prevMonth = new Date(currentDate.getFullYear(), currentDate.getMonth(), 0);
        days += prevMonth.getDate();
    }
    if (months < 0) {
        years -= 1;
        months += 12;
    }

    return { years, months, days };
}

  const experience = calculateExperience(2018);
  const words = ['Coming Soon', 'Front End ', 'JavaScript', 'MERN Stack', 'AI/ML','System Design'];




  return(<>
    <div id="home" className="header_hero">
        <div className="container">
            <div className="row align-items-center justify-content-center justify-content-lg-between">
                <div className="col-lg-6">
                    <div className="header_hero_content mt-45">
                        <h5 className="header_sub_title wow fadeInUp" data-wow-duration="1.3s" data-wow-delay="0.2s" style={{visibility:"visible",animationDuration:"1.3s",animationDelay:"0.2s",animationName:"fadeInUp"}}>Hello I'm</h5>
                        <h2 className="header_title wow fadeInUp" data-wow-duration="1.3s" data-wow-delay="0.5s" style={{visibility:"visible",animationDuration:"1.3s",animationDelay:"0.5s",animationName:"fadeInUp"}}>Harish</h2>
                        <p className="wow fadeInUp" data-wow-duration="1.3s" data-wow-delay="1.1s" style={{visibility:"visible",animationDuration:"1.3s",animationDelay:"1.1s",animationName:"fadeInUp" }}>
                          {`Empowering Students Through Projects, Practical Learning, and AI Innovation, Led by a Professional Full Stack Developer with ${experience.years}+ Years of Experience`}
                        </p>

                    </div>
                </div>
                <div className="col-lg-6 col-md-6 col-sm-7">
                    <div className="header_hero_image mt-50 wow typing_text" >
                      <TypeWriter words={words}  />
                    </div>
                </div>
            </div>
        </div>

    </div>

      </>)

}

export default HeaderHero;
