import React,{useState} from "react";
import logo from "../../assets/images/logo-sm-dark.png";
import {Link} from "react-router-dom";
import {post} from "../../helpers/api_helpers";
import {Container,Row,Col,Form,FormGroup,Label,Input,Alert} from "reactstrap";
const ForgetPassword =(props)=>{
  const [user,setUser] = useState({email:""});
  const [error,setError] = useState({email:""});
  const [serverMessage,setServerMessage] = useState(null);
  const backendUrl = process.env.NODE_ENV === "development"?(process.env.REACT_APP_BACKEND_URL):(process.env.REACT_APP_BACKEND_URL_PRODUCTION)

  // handling the Change
  const handleChange = (e)=>{
    const name = e.target.name;
    const value = e.target.value;
    setUser({...user,[name]:value})
    setError({...error,[name]:""})
  }

  // submitting the Form
  const submitForm = (e)=>{
    e.preventDefault();
    if(user.email.trim() === ""){
      error.email = "Email Field Cannot Be Empty";
      setError({...error,error})
    }
    else{
      post(backendUrl+'/api/auth/passwordReset',user).then((res)=>setServerMessage(res.message))
    }

  }
  return(<React.Fragment>
            <div className="account-pages my-5 pt-sm-5">
              <Container>
                <Row className="justify-content-center">
                  <Col md={8} lg={6} xl={5}>
                    <div className="card overflow-hidden">
                      <div className="bg-login text-center">
                        <div className="bg-login-overlay"></div>
                        <div className="position-relative">
                          <h5 className="text-white font-size-20">Reset Password</h5>
                          <p className="text-white-50 mb-0">Reset-Password </p>
                          <a href="/" className="logo logo-admin ">
                            <img src={logo} alt="" height="30" />
                          </a>
                        </div>
                      </div>
                      <div className="card-body pt-5">
                        <div className="p-2">
                          <Form onSubmit={submitForm} className="form-horizontal">
                            <FormGroup className="mb-3">

                              {serverMessage? (
                                  <Alert color="success" className="text-center mb-4" style={{ marginTop: "13px" }}>
                                      {serverMessage}
                                    </Alert>
                                  ) : null
                                }

                               <Label for="exampleEmail">Email</Label>
                               <Input type="email" name="email"  placeholder="Enter Email" onChange={handleChange}  />
                                 {error.email.trim() !== ""?
                                   (<div className="alert alert-danger" role="alert">
                                     {error.email}
                                 </div>):(<div></div>)}
                            </FormGroup>
                            <div className="mt-3">
                              <button className="btn btn-primary w-100 waves-effect waves-light" type="submit">
                                 Reset
                               </button>
                            </div>
                          </Form>
                        </div>
                      </div>
                    </div>
                    <div className="mt-5 text-center">
                      <p>Remember It ?
                          <Link to="/" className="fw-medium text-primary">
                              Sign In here
                          </Link>
                      </p>
                    </div>
                  </Col>
                </Row>
              </Container>
            </div>
        </React.Fragment>)
}

export default ForgetPassword;
