import {createContext,useReducer} from "react";
const initalState = {
  email:null,
  token: null,
  loading: true,
  errorMessage: null,
  isAuthenticated:false,
  expiryTime:null,
  role:[]
}
export const AuthStateContext = createContext();
export const AuthDispatchContext = createContext();

const reducer = (state,action)=>{
    switch (action.type) {
        case "LOGIN_SUCCESS":
          return{
            ...state,
            email:action.payload.email,
            token:action.payload.accessToken,
            loading:false,
            errorMessage:null,
            isAuthenticated:action.payload.status === "Pending"?(false):(true),
            expiryTime:action.payload.exp,
            role:action.payload.role

          }
        case "SET_USER_LOGIN":
          return{
            ...state,
            email:action.payload.email,
            token:action.payload.accessToken,
            loading:false,
            errorMessage:null,
            isAuthenticated:action.payload.status === "Pending"?(false):(true),
            expiryTime:action.payload.exp,
            role:action.payload.role
          }
        case "LOGOUT_SUCCESS":
          return{
            ...state,
            email:null,
            token:null,
            loading:true,
            errorMessage:null,
            isAuthenticated:false,
            expiryTime:null,
            role:null,
          }

        case "LOGIN_ERROR":
          return {
            ...state,
            loading: false,
            errorMessage: action.payload
          };
        default:
        throw new Error(`Unhandled action type: ${action.type}`)
    }
}


export function setUserLogin(dispatch, accessToken,refreshToken){
      if(accessToken && refreshToken){
        let data = parseJwt(accessToken)
        data = {...data,accessToken}
        dispatch({ type: 'SET_USER_LOGIN', payload: data });
      }

}



export async function loginSuccess(dispatch, accessToken,refreshToken){
  if(accessToken && refreshToken){
    let data = parseJwt(accessToken)
    data = {...data,accessToken}
    dispatch({ type: 'LOGIN_SUCCESS', payload: data });
    localStorage.setItem('refresh',refreshToken)
    localStorage.setItem('access',accessToken)
  }
}
export function setAccessToken(dispatch,accessToken){
    if(accessToken){
        localStorage.setItem('access',accessToken)
    }
}



export function loginError(dispatch,errMessage){
        dispatch({ type: 'LOGIN_ERROR',payload:errMessage });
}


export function logoutSuccess(dispatch){
        localStorage.removeItem('access')
        localStorage.removeItem('refresh')
        dispatch({ type: 'LOGOUT_SUCCESS' });

}



export const parseJwt=(token)=> {
      var base64Payload = token.split('.')[1];
      var payload = atob(base64Payload)
      return JSON.parse(payload.toString());
}


const AuthProvider = ({children})=>{
  const [state,dispatch] = useReducer(reducer,initalState);
  return(<AuthDispatchContext.Provider value={dispatch}>
            <AuthStateContext.Provider value={state}>
              {children}
            </AuthStateContext.Provider>
        </AuthDispatchContext.Provider>)
}

export default AuthProvider;
