import React, { useEffect, useState, useRef } from 'react';
import './header.css';

const TypeWriter = ({ words, wait = 250 }) => {
  const [wordIndex, setWordIndex] = useState(0);
  const [text, setText] = useState('');
  const indexRef = useRef(0);

  function typeWriter() {
    const currentWord = words[wordIndex];
    if (indexRef.current < currentWord.length) {

      setText((prev) => prev + currentWord[indexRef.current]);
      indexRef.current++;
    } else {

      indexRef.current = 0;
      let tempWordIndex = wordIndex + 1;

      if (tempWordIndex >= words.length) {
        tempWordIndex = 0;
      }

      setWordIndex(tempWordIndex);
      setText('');
    }
  }

  useEffect(() => {
    const interval = setInterval(() => {
      typeWriter();
    }, wait);

    return () => clearInterval(interval);
  }, [wordIndex, wait]);

  return (
    <span className="typewriter-container">
      <h1>{text}</h1>
    </span>
  );
};

export default TypeWriter;
