import React from "react";
import {Modal,ModalHeader,ModalBody,ModalFooter} from "reactstrap";
const CategoryModal =(props)=>{
  return(<>
          <Modal isOpen={props.modal} toggle={()=>props.setModal(!props.modal)} >
            <ModalHeader>Create Category</ModalHeader>
            <ModalBody>
              <div className="mb-3">
                <label htmlFor="category" className="form-label">Category Title</label>
                <input type="text" className="form-control" />
              </div>
              <div className="mb-3">
                <label htmlFor="upload" className="form-label">Upload Image</label>
                <input type="file" className="form-control" />
              </div>
            </ModalBody>
            <ModalFooter>
                <button  onClick={()=>props.setModal(!props.modal)} className="btn btn-danger">Cancel</button>
                <button  className="btn btn-primary">Submit</button>
            </ModalFooter>
          </Modal>
        </>)
}

export default CategoryModal;
