import {createContext,useReducer} from "react";
const INIT_STATE = {
  layoutType: "vertical",
  layoutWidth: "fluid",
  leftSideBarTheme: "light",
  leftSideBarType: "default",
  topbarTheme: "colored",
  isPreloader: false,
  showRightSidebar: false,
  isMobile: false,
  showSidebar: true,
  leftMenu: false,
}
export const LayoutStateContext = createContext();
export const LayoutDispatchContext = createContext();

const reducer = (state,action)=>{
  switch(action.type){
    case "SHOW_RIGHT_SIDEBAR":
      return {
        ...state,
        showRightSidebar:action.payload,
      }
      case "CHANGE_LAYOUT":
      return{
          ...state,
          layoutType: action.payload
      }
      case "CHANGE_TOPBAR_THEME":
      return {
          ...state,
          topbarTheme:action.payload

      }
      case "TOGGLE_LEFTMENU":
      return {
        ...state,
        leftMenu: action.payload
      }
      case "CHANGE_SIDEBAR_TYPE":
      return{
        ...state,
        leftSideBarType: action.payload.sidebarType
      }
      case "CHANGE_SIDEBAR_THEME":
      return {
        ...state,
        leftSideBarTheme: action.payload,
      }
      case "CHANGE_LAYOUT_WIDTH":
      return {
        ...state,
        layoutWidth: action.payload,
        }

      case "CHANGE_PRELOADER":
        return{
          ...state,
          isPreloader:action.payload

        }
    default:
      return state;
  }
}

// helper Function To Show the Right Sidebar
export function showRightSidebarAction(dispatch,payload){
    dispatch({type: 'SHOW_RIGHT_SIDEBAR',payload:payload});
    manageBodyClass("right-bar-enabled", "add")
}


function manageBodyClass(cssClass, action = "toggle"){
  switch(action){
      case "add":
        if(document.body) document.body.classList.add(cssClass)
        break;
      case "remove":
        if(document.body) document.body.classList.remove(cssClass)
        break;
      default:
        if(document.body) document.body.classList.toggle(cssClass)
  }


}



// helper Function To Change layoutType
export function changeLayout(dispatch,payload){
  dispatch({type:'CHANGE_LAYOUT',payload:payload})
  changeBodyLayout(dispatch,payload)

}


// check if the selected is horizontal or Vertical
function changeBodyLayout(dispatch,payload){
  if (payload === "horizontal") {
      changeTopbarThemeAction(dispatch,"colored")
      document.body.removeAttribute("data-sidebar")
      manageBodyClass("vertical-collpsed", "remove")
      document.body.removeAttribute("data-sidebar-size")
  }
  else{
    changeTopbarThemeAction(dispatch,"colored")
  }
  changeBodyAttribute("data-layout",payload)
}

// helper function
function changeTopbarThemeAction(dispatch,topbarTheme){
    dispatch({type:'CHANGE_TOPBAR_THEME',payload: topbarTheme})
    manageBodyClass("vertical-collpsed", "remove")
    changeBodyAttribute("data-topbar", topbarTheme)

}

function changeBodyAttribute(attribute, value) {
  if (document.body) document.body.setAttribute(attribute, value)
  return true
}

// changing the Left menubar
export function toggleLeftmenu(dispatch,isOpen){
  dispatch({type:"TOGGLE_LEFTMENU",payload:isOpen})
}

// changing the Sidebar Type

export function changeSidebarType(dispatch,sidebarType, isMobile){
  dispatch({type:"CHANGE_SIDEBAR_TYPE",payload:{sidebarType,isMobile}})
  changeLeftSidebarType(sidebarType,isMobile)
}

function changeLeftSidebarType(sidebarType, isMobile){
  switch(sidebarType){
    case "compact":
      changeBodyAttribute("data-sidebar-size", "small")
      manageBodyClass("sidebar-enable", "remove")
      manageBodyClass("vertical-collpsed", "remove")
      break
    case "icon":
      changeBodyAttribute("data-keep-enlarged", "true")
      manageBodyClass("vertical-collpsed", "add")
      break
    case "condensed":
      manageBodyClass("sidebar-enable", "add")
      if (window.screen.width >= 992) {
          manageBodyClass("vertical-collpsed", "remove")
          manageBodyClass("sidebar-enable", "remove")
          manageBodyClass("vertical-collpsed", "add")
          manageBodyClass("sidebar-enable", "add")
      }
      else{
          manageBodyClass("sidebar-enable", "add")
          manageBodyClass("vertical-collpsed", "add")
      }
      break
    default:
      changeBodyAttribute("data-sidebar-size", "")
      manageBodyClass("sidebar-enable", "remove")
      if (!isMobile)
        manageBodyClass("vertical-collpsed", "remove")
      break


  }


}

// changing the Sidebar theme
export function  changeSidebarTheme(dispatch,theme){
    dispatch({type:"CHANGE_SIDEBAR_THEME",payload:theme})
    changeLeftSidebarTheme(theme)
}


function changeLeftSidebarTheme(theme) {
    changeBodyAttribute("data-sidebar", theme)
}
// change Layout Width

export function changeLayoutWidth(dispatch,width){
  dispatch({type:"CHANGE_LAYOUT_WIDTH",payload:width})
  changeLayoutWidthHelper(dispatch,width)
}



// helper fucntion
function changeLayoutWidthHelper(dispatch,width) {
    if (width === "boxed") {
      changeSidebarType(dispatch,"icon");
      changeBodyAttribute("data-layout-size", width)
      changeBodyAttribute("data-layout-scrollable", false)
    } else if (width === "scrollable") {
      changeSidebarType(dispatch,"default")
      changeBodyAttribute("data-layout-scrollable", true)
    } else {
      changeSidebarType(dispatch,"default")
      changeBodyAttribute("data-layout-size", width)
      changeBodyAttribute("data-layout-scrollable", false)
    }
}

// chagne topBar theme
export function changeTopbarTheme(dispatch,topbarTheme){
  dispatch({type:"CHANGE_TOPBAR_THEME",payload:topbarTheme})
  changeTopbarThemeAction(dispatch,topbarTheme)

}
//pre Loader  function
export function changePreloader(dispatch,layout){
  dispatch({type:"CHANGE_PRELOADER",payload:layout})
}



const LayoutProvider = ({children})=>{
  const [state,dispatch] = useReducer(reducer,INIT_STATE);
  return(<LayoutDispatchContext.Provider value={dispatch}>
            <LayoutStateContext.Provider value={state}>
              {children}
            </LayoutStateContext.Provider>
          </LayoutDispatchContext.Provider>)
}

export default LayoutProvider;
